

























































































.article-card {
  cursor: pointer;
  line-height: 1.4rem;
  margin: 0 auto;
  width: 100%;

  h1 {
    font-size: 1.1rem;
    line-height: 1.55;
  }

  &.recently-published {
    &:before {
      content: 'New';
      position: absolute;
      top: -2px;
      right: -21px;
      background: #FF545E;
      color: #fff;
      padding: 10px 20px 4px 20px;
      font-weight: bold;
      font-size: 0.7rem;
      transform: rotate(45deg);
      line-height: 1;
      z-index: 2;
    }
  }
}
.mobile-wrapper {
  .article-card {
    margin-bottom: 20px;
  }
}
